.scroll {
  height: 200px;
  padding: 25px;
  margin: 20px 0;
  background-color: #000000;
  background-size: cover;
  overflow: hidden;
  -webkit-perspective: 100px;
          perspective: 100px;
}

.scroll-container {
  position: relative;
  height: 200px;
  -webkit-animation: scroll-up 45s linear infinite;
          animation: scroll-up 45s linear infinite;
  color: #EDDF00;
  font-size: 2em;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transform-origin: 50% 100%;
      -ms-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}

.intro-line {
  padding-bottom: 400px;
  color: #485F95;
}

.scroll-date {
  padding-bottom: 50px;
}

.opening-scroll {
  text-align: justify;
}

@media only screen and (max-width: 550px) {

  .scroll-container {
    font-size: 1em;
  }

}

@-webkit-keyframes scroll-up {
  0% {
    -webkit-transform: rotateX(20deg) translateY(100%);
            transform: rotateX(20deg) translateY(100%);
  }
  100% {
    -webkit-transform: rotateX(25deg) translateY(-900%);
            transform: rotateX(25deg) translateY(-900%);
  }
}

@keyframes scroll-up {
  0% {
    -webkit-transform: rotateX(20deg) translateY(100%);
            transform: rotateX(20deg) translateY(100%);
  }
  100% {
    -webkit-transform: rotateX(25deg) translateY(-900%);
            transform: rotateX(25deg) translateY(-900%);
  }
}



